import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyAlphaNum, WindowComponent } from 'summer';
import { CLASS_NAME, ClassificacaoServico } from '../../../../models/dtos/ger/classificacaoServico';

interface ClassificacaoServicoEditViewProps extends EditViewProps<ClassificacaoServico> {}

const ClassificacaoServicoEditView: FC<ClassificacaoServicoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<ClassificacaoServico>({ defaultValues: selectedItem, mode: 'onBlur' });

  const defaultWidth: number = 100;

  return (
    <WindowComponent title="Classificação de Serviços" wikiId={2517}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, ...onlyAlphaNum(0, 4) } }}
              id="codigo"
              isPrependLabel={true}
              inputProps={{ autoFocus: true }}
              label="Código"
              labelWidth={defaultWidth}
              placeholder="Informe o Código"
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{
                ...formProps,
                validation: {
                  maxLength: 4,
                  pattern: {
                    message: 'Somente são permitidos números ou letras.',
                    value: new RegExp(`^[a-zA-Z0-9 ]{0,4}$`),
                  },
                },
              }}
              id="codigoAtividade"
              isPrependLabel={true}
              label="Código da Atividade"
              placeholder="Informe o Código da Atividade"
              tooltipProps={{ message: 'Campo utilizado na geração da Giss Online' }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 300 } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              labelWidth={defaultWidth}
              placeholder="Informe a Descrição"
              type={InputType.TEXT_AREA}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default ClassificacaoServicoEditView;
